#sidebar {
  position: absolute;
  right: -600px;
  bottom: 38px;
  background-color: whitesmoke;
  opacity: 0.93;
  transition-duration: 0.4s;
}
.sidebar-bottom {
  position: absolute;
  bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.sidebar-btns {
  width: 70px;
  height: 57px;
  margin: 7px;
  background-color: white;
  background-position: center;
  background-size: 45%;
  background-repeat: no-repeat;
  border-radius: 10px;
  opacity: 1;
}
.sound,
.brightness {
  width: 90%;
  display: flex;
  align-items: center;
}

#volume,
#light {
  width: 245px;
  height: 6px;
}

#sound-icon,
#light-icon {
  margin: 15px;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  background-color: white;
  background-position: center;
  background-size: cover;
}

#light-icon {
  background-image: url("../images/brightness.png");
}
#sound-icon {
  background-image: url("../images/sound.png");
}

#wifi-btn {
  background-image: url("../images/wifi.jpg");
}

#battery-btn {
  background-image: url("../images/battery.jpg");
}

#setting-btn {
  background-image: url("../images/settings.png");
}
#theme-change {
  background-image: url("../images/night-mode.png");
}
#bluetooth {
  background-image: url("../images/bluetooth.png");
}
#airplane {
  background-image: url("../images/airplane.jpg");
  background-size: 55%;
}
